@import "../../node_modules/slick-carousel/slick/slick.css";
@import "../../node_modules/slick-carousel/slick/slick-theme.css";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

li {
  list-style: none;
}

body {
  background-color: #FFFFFF;
  font-family: 'Satoshi', sans-serif;
  color: #0E231F;
  font-size: 18px;
  padding: 12px;
  overflow-x: hidden;
}

.header {
  display: flex;
  justify-content: space-between;
  width: 100%;
  color: #ffffff;
  padding: 24px;
  position: absolute;
  z-index: 11;

  .logo {
    display: flex;
    align-items: center;
    gap: 3px;

    .fixed{
      position: fixed;
      width: 32px;
      left: 36px;
      top: 28px;
    }
  }

  .header-info {
    display: flex;
    align-items: center;
    gap: 20px;
    position: fixed;
    right: 36px;
    overflow: hidden;
  }

  .burger {
    background: #FFFFFF;
    border-radius: 24px;
    width: 44px;
    height: 44px;
    padding: 12px;

    &.fixed {
      background: #0E231F;
      top: 36px;
      right: 36px;
      transition: .6s all ease-in-out;
      z-index: 99;
    }

    &:hover {
      cursor: pointer;
    }
  }
}

.flex {
  display: flex;

  &-end {
    justify-content: flex-end;
  }

  &-start {
    justify-content: flex-start;
  }
}

