.coverBg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  opacity: 0.95;
  background: linear-gradient(0deg, #D2D0CE, #D2D0CE), rgba(71, 71, 71, 0.84);
