.dna-container {
  padding-right: 0;
  padding-top: 116px;
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.slider-dna {
  button.slick-arrow {
    position: absolute;
    bottom: -100px;
    top: unset;
    right: 206px;
    left: unset;
    width: 44px;
    height: 44px;
    padding: 15px;
  }
  .slick-prev{
    right: 236px !important;
  }
  .slick-track {
    display: flex !important;
    gap: 8px;
    width: 100%;
    color: #fff;
    align-items: flex-start;
  }

  .slick-slide {
    width: fit-content !important;
    opacity: 0.2;
    transform: none;
    position: unset;
    transition: 1s all ease-in-out;

    &:not(.slick-slide.slick-active ~ .slick-slide) {
      transform: scale3d(.3, .3, .3);
      transition: 1s all ease-in-out;
      position: absolute;
      bottom: 20px;
      width: 100vw;
      //height: 100vh;
      z-index: -1;
    }

    &.slick-active {
      opacity: 1;
      transform: none;
      position: unset;
      transition: 1s all ease-in-out;

      ~ .slick-slide {
        transform: none;
        position: unset;
        transition: 1s all ease-in-out;
      }
    }
  }

  .slider-item {
    display: flex !important;
    flex-direction: column;
    gap: 8px;
    justify-content: flex-end;
    width: fit-content;
    max-width: 491px;
    height: 424px;
    left: 0;
    top: 0;
    padding: 0 0 32px 32px;

    background: rgba(102, 129, 145, 0.56);
    border-radius: 16px;

    .title-dna {
      font-weight: 400;
      font-size: 40px;
      line-height: 140%;
    }

    .desc-dna {
      padding-right: 100px;
      //max-width: 356px;
    }

    img {

      height: 100%;
    }
  }
}
.contactUs {
  width: fit-content;
  opacity: 1;
  transition: 1s all ease-in-out;

  &.hideScroll {
    opacity: 0;
    transform: translateX(100px);
    transition: 1s all ease-in-out;
  }
}


.quote-reviews {
  display: flex;
  gap: 96px;

  .quote-slider {
    max-width: 850px;

    button.slick-arrow {
      position: absolute;
      bottom: 0;
      top: unset;
      right: 35px;
      left: unset;
      width: 44px;
      height: 44px;
      padding: 15px;
    }

    .slick-prev {
      right: 85px !important;
    }

    .slick-prev:before, .slick-next:before {
      font-size: 40px;
    }

    button {
      //display: none !important;
    }

    .slick-list {
      font-weight: 400;
      font-size: 40px;
      line-height: 140%;
    }

    .slick-dots {
      display: flex !important;
      justify-content: space-between;
      position: relative;

      li {
        width: 100%;

        .review-info {

        }

        .avatar {
          height: 96px;
          //transform: scale(1);
          display: flex;
          align-items: center;

          img {
            width: 48px;
            opacity: 0.4;
          }
        }

        .hide {
          display: flex;
          transform: scale(0);
          flex-direction: column;
          gap: 48px;
          opacity: 0;
          margin-top: 16px;
          transition: 1s all ease-in-out;
          overflow: hidden;
          position: absolute;
        }

        &.slick-active {

          .review-info {
            .avatar {
              transition: 1s all ease-in-out;
            }

            img {
              width: 96px;
              opacity: 1;
              transition: 1s all ease-in-out;
            }
          }

          .hide {
            transform: scale(1);
            opacity: 1;
            transition: 1s all ease-in-out;
          }
        }
      }

      a {
        text-align: left;
        display: block;
      }
    }

    .name {
      font-weight: 700;
      font-size: 18px;
      line-height: 180%;
    }

    .job-title {
      font-weight: 400;
      font-size: 14px;
      line-height: 170%;
    }
  }
}

.sidebar-menu {
  transform: translateX(450px);
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  max-width: 416px;
  height: calc(100vh - 24px);
  background: #0E231F;
  border-radius: 16px;
  padding: 24px;
  transition: .6s all ease-in-out;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  z-index: 99;

  &.active {
    transform: translateX(0);
    transition: .6s all ease-in-out;
  }

  .sidebar-menu-header {
    display: flex;
    justify-content: space-between;

    .icon-close {
      padding: 12px;
      background: #60605C;
